<template>
	<div class="container-two">
		<div class="main-banner" :style="background">
			<div class="main-banner-first font-name">
				<div class="main-banner-first-icon">
					<img class="img-icon-top" src="../assets/mine.png" alt="Рудник" />
					<div>{{ counterMine }}</div>
				</div>
				<div class="main-banner-first-icon">
					<img class="img-icon-top" src="../assets/city.png" alt="Город" />
					<div>{{ counterCity }}</div>
				</div>
				<div class="main-banner-first-icon">
					<img class="img-icon-top" src="../assets/money.png" alt="Монета" />
					<div>{{ counterAll }}</div>
				</div>
				<div class="main-banner-first-icon">
					<img
						class="img-icon-top"
						src="../assets/field.png"
						alt="Поле пшена"
					/>
					<div>{{ counterFarm }}</div>
				</div>
				<div class="main-banner-first-icon">
					<img class="img-icon-top" src="../assets/strenght.png" alt="Сила" />
					<div>{{ counterEnergy }}</div>
				</div>
			</div>
			<div class="main-banner-second">
				<div class="arrow-left" @click="leftClick">
					<svg
						:style="colorSvg"
						class="img-arrow"
						width="84"
						height="108"
						viewBox="0 0 84 108"
						fill="currentColor"
						xmlns="http://www.w3.org/2000/svg"
					>
						<path
							d="M1.2031 53.3626C0.742692 53.6628 0.74269 54.3372 1.2031 54.6374L82.3529 107.551C82.9872 107.965 83.7931 107.3 83.5032 106.601C80.2435 98.7354 70.1125 72.7792 70.1124 54.0007C70.1122 35.2217 80.2435 9.26472 83.5032 1.39916C83.7931 0.699662 82.9872 0.0349661 82.3529 0.448538L1.2031 53.3626Z"
							fill="currentColor"
						/>
						<path
							d="M1.2031 53.3626C0.742692 53.6628 0.74269 54.3372 1.2031 54.6374L82.3529 107.551C82.9872 107.965 83.7931 107.3 83.5032 106.601C80.2435 98.7354 70.1125 72.7792 70.1124 54.0007C70.1122 35.2217 80.2435 9.26472 83.5032 1.39916C83.7931 0.699662 82.9872 0.0349661 82.3529 0.448538L1.2031 53.3626Z"
							stroke="currentColor"
						/>
					</svg>
				</div>
				<img :src="picture" class="skull" />
				<div class="arrow-right" @click="rightClick">
					<svg
						:style="colorSvg"
						class="img-arrow"
						width="84"
						height="108"
						viewBox="0 0 84 108"
						fill="currentColor"
						xmlns="http://www.w3.org/2000/svg"
					>
						<path
							d="M82.7979 53.3626C83.2583 53.6628 83.2583 54.3372 82.7979 54.6374L1.64807 107.551C1.01381 107.965 0.20792 107.3 0.497814 106.601C3.75747 98.7354 13.8885 72.7792 13.8886 54.0007C13.8887 35.2217 3.7575 9.26472 0.49781 1.39916C0.207924 0.699661 1.01381 0.0349653 1.64807 0.448537L82.7979 53.3626Z"
							fill="currentColor"
						/>
						<path
							d="M82.7979 53.3626C83.2583 53.6628 83.2583 54.3372 82.7979 54.6374L1.64807 107.551C1.01381 107.965 0.20792 107.3 0.497814 106.601C3.75747 98.7354 13.8885 72.7792 13.8886 54.0007C13.8887 35.2217 3.7575 9.26472 0.49781 1.39916C0.207924 0.699661 1.01381 0.0349653 1.64807 0.448537L82.7979 53.3626Z"
							stroke="currentColor"
						/>
					</svg>
				</div>
			</div>
			<div class="main-banner-third">
				<div class="font-name">
					Казна
				</div>
				<div class="line font-name">
					<div class="black-box" :style="backgroundBox" @click="decreaseFive">
						-5
					</div>
					<div class="black-box" :style="backgroundBox" @click="decrease">
						-
					</div>
					<div class="black-box-center" :style="backgroundBox">
						{{ counter }}
					</div>
					<div class="black-box" :style="backgroundBox" @click="add">+</div>
					<div class="black-box" :style="backgroundBox" @click="addFive">
						+5
					</div>
				</div>
			</div>
			<div class="main-banner-fourth">
				<div
					class="main-banner-btn font-name"
					:style="backgroundBox"
					@click="nextBtnClick"
				>
					<div class="main-banner-btn-text">Следующий ход</div>
					<div class="main-banner-btn-number">{{ counterNext }}</div>
				</div>
			</div>
		</div>
	</div>
</template>

<script>
export default {
	computed: {
		picture() {
			return require(`@/assets/${this.image}`)
		},
		background() {
			return `background-color: ${this.color}`
			// return 'background-color:' + this.color
		},
		backgroundBox() {
			return `background-color: ${this.boxColor}`
			// return 'background-color:' + this.color
		},
		colorSvg() {
			return `color: ${this.boxColor}`
			// return 'background-color:' + this.color
		},
	},
	components: {},
	data() {
		return {
			counter: 35,
			counterMine: 0,
			counterCity: 0,
			counterAll: 0,
			counterFarm: 0,
			counterEnergy: 0,
			counterNext: 1,
			image: 'skull.png',
			color: '#26322B',
			boxColor: '#030F08',
			arrList: [
				{
					image: 'skull.png',
					color: '#26322B',
					boxColor: '#030F08',
				},
				{
					image: 'machine.png',
					color: '#442E22',
					boxColor: '#250D00',
				},
				{
					image: 'wave.png',
					color: '#632E2E',
					boxColor: '#410000',
				},
				{
					image: 'hieroglyph.png',
					color: '#4E2650',
					boxColor: '#2F0031',
				},
				{
					image: 'sun.png',
					color: '#2F747B',
					boxColor: '#096069',
				},
				{
					image: 'coffee.png',
					color: '#56585B',
					boxColor: '#201D23',
				},
				{
					image: 'tree.png',
					color: '#295455',
					boxColor: '#003334',
				},
				{
					image: 'lion.png',
					color: '#395696',
					boxColor: '#072C7C',
				},
				{
					image: 'indian.png',
					color: '#825602',
					boxColor: '#3B2700',
				},
				{
					image: 'jellyfish.png',
					color: '#005A90',
					boxColor: '#003555',
				},
			],
			index: 0,
		}
	},
	methods: {
		decreaseFive() {
			this.counter = this.counter - 5
			if (this.counter < 0) {
				this.counter = 0
			}
		},
		decrease() {
			this.counter = this.counter - 1
			if (this.counter < 0) {
				this.counter = 0
			}
		},
		add() {
			this.counter = this.counter + 1
		},
		addFive() {
			this.counter = this.counter + 5
		},
		leftClick() {
			console.log('Клик влево')
			if (this.index > 0) {
				this.index = this.index - 1
				this.color = this.arrList[this.index].color
				this.image = this.arrList[this.index].image
				this.boxColor = this.arrList[this.index].boxColor
			} else {
				this.index = this.arrList.length - 1
				this.color = this.arrList[this.index].color
				this.image = this.arrList[this.index].image
				this.boxColor = this.arrList[this.index].boxColor
			}
		},
		rightClick() {
			console.log('Клик вправо')
			if (this.index < this.arrList.length - 1) {
				this.index = this.index + 1
				this.color = this.arrList[this.index].color
				this.image = this.arrList[this.index].image
				this.boxColor = this.arrList[this.index].boxColor
			} else {
				this.index = 0
				this.color = this.arrList[this.index].color
				this.image = this.arrList[this.index].image
				this.boxColor = this.arrList[this.index].boxColor
			}
		},
		nextBtnClick() {
			console.log('Клик вправо')
			this.counterNext = this.counterNext + 1
		},
	},
}
</script>

<style>
.main-banner {
	width: 82vw;
	height: auto;
	display: flex;
	flex-direction: column;
	background-color: #26322b;
	justify-content: flex-start;
	padding: 0 9vw 0 9vw;
}
.main-banner-first {
	display: flex;
	justify-content: space-between;
	margin: 5px 0 20px 0;
	padding: 0 20px;
}
.font-name {
	color: #ffffff;
	text-transform: uppercase;
	/* font-size: 16px; */
	/* font-weight: 600; */
	align-items: center;
}
.main-banner-first-icon {
	display: flex;
	flex-direction: column;
	align-self: flex-start;
}
.img-icon-top {
	width: 50px;
	height: 50px;
	position: relative;
	bottom: -2px;
}
.main-banner-second {
	display: flex;
	justify-content: center;
	align-items: center;
}
.arrow-left {
	margin: 0 5px 0 0;
}
.skull {
	width: 115px;
}
.arrow-right {
	margin: 0 0 0 5px;
}
.img-arrow {
	width: 29px;
}
.main-banner-third {
	display: flex;
	flex-direction: column;
	padding: 0 20px;
}
.main-banner-third > div:first-child {
	margin: 11px 0 6px 0;
}
.line {
	display: flex;
	flex-direction: row;
	justify-content: space-between;
}
.black-box {
	background-color: #030f08;
	color: #ffffff;
	width: 41px;
	height: 38px;
	border-radius: 6px;
	display: flex;
	justify-content: center;
	align-items: center;
}
.black-box:last-child {
	margin-right: 0;
}
.black-box-center {
	background-color: #030f08;
	color: #ffffff;
	width: 52px;
	height: 48px;
	border-radius: 6px;
	display: flex;
	justify-content: center;
	align-items: center;
}
.main-banner-fourth {
	display: flex;
	justify-content: center;
	margin: 5px 20px 10px 20px;
}
.main-banner-btn {
	background-color: #030f08;
	width: 100%;
	height: auto;
	text-align: center;
	border-radius: 8px;
	line-height: 300%;
	padding: 0 18px;
	display: flex;
	margin-bottom: 2px;
	height: 48px;
}
.main-banner-btn-text {
	flex: 8;
	display: flex;
}
.main-banner-btn-number {
	flex: 2;
	display: flex;
	justify-content: flex-end;
}
</style>
