<template>
	<div class="modal-backdrop">
		<div class="modal">
			<header class="modal-header">
				<slot name="header">
					This is the default title!
				</slot>
				<button type="button" class="btn-close" @click="confirmNo">
					x
				</button>
			</header>

			<section class="modal-body">
				<slot name="body"></slot>
			</section>

			<footer class="modal-footer">
				<div v-if="isConfirm" class="modal-footer-btn">
					<button type="button" class="btn-green" @click="confirmNo">
						Нет
					</button>
					<button type="button" class="btn-green" @click="confirmYes">
						Да
					</button>
				</div>

				<slot name="footer"></slot>
			</footer>
		</div>
	</div>
</template>

<script>
export default {
	name: 'Modal',
	props: {
		isConfirm: {
			type: Boolean,
			default: false,
		},
	},
	methods: {
		confirmNo() {
			this.$emit('close')
		},
		confirmYes() {
			this.$emit('yes')
		},
	},
}
</script>

<style>
.modal-backdrop {
	position: fixed;
	top: 0;
	bottom: 0;
	left: 0;
	right: 0;
	background-color: rgba(0, 0, 0, 0.3);
	display: flex;
	justify-content: center;
	align-items: center;
	z-index: 1;
}

.modal {
	background: #ffffff;
	box-shadow: 2px 2px 20px 1px;
	overflow-x: auto;
	display: flex;
	flex-direction: column;
	width: 70%;
	border-radius: 8px;
}

.modal-header,
.modal-footer {
	padding: 15px;
	display: flex;
}

.modal-header {
	position: relative;
	color: #6b5858;
	justify-content: center;
}

.modal-footer {
	flex-direction: column;
	justify-content: flex-end;
}

.modal-footer-btn {
	display: flex;
	justify-content: space-around;
}

.modal-body {
	position: relative;
	padding: 20px 10px;
}

.btn-close {
	position: absolute;
	top: 0;
	right: 0;
	border: none;
	font-size: 20px;
	padding: 10px;
	cursor: pointer;
	font-weight: bold;
	color: #6b5858;
	background: transparent;
}

.btn-green {
	color: #6b5858;
	background: #d9d9d9;
	border: 1px solid #d9d9d9;
	border-radius: 6px;
	padding: 8px 20px;
	width: 70px;
}
</style>
