<template>
	<div class="line bg-orange" :style="background">
		<div class="img-container">
			<img :src="picture" alt="Поле пшена" class="img-icon" />
		</div>
		<div class="label-container">
			<div class="font-name">{{ text }}</div>
		</div>
		<div class="count-container">
			<img
				src="../assets/left-transparent.png"
				class="arrow-size"
				@click="decrease"
			/>
			<div>{{ counter }}</div>
			<img
				src="../assets/right-transparent.png"
				class="arrow-size"
				@click="add"
			/>
		</div>
	</div>
</template>

<script>
export default {
	props: {
		image: {
			type: String,
			default: '',
		},
		text: {
			type: String,
			default: '',
		},
		color: {
			type: String,
			default: '',
		},
	},
	computed: {
		picture() {
			return require(`@/assets/${this.image}`)
		},
		background() {
			return `background-color: ${this.color}`
			// return 'background-color:' + this.color
		},
	},
	components: {},
	data() {
		return {
			counter: 0,
		}
	},
	methods: {
		decrease() {
			this.counter = this.counter - 1
			if (this.counter < 0) {
				this.counter = 0
			}
		},
		add() {
			this.counter = this.counter + 1
		},
	},
}
</script>

<style scoped>
.label-container {
	display: flex;
	justify-content: center;
	flex: 6;
}
.img-container {
	display: flex;
	justify-content: flex-start;
	flex: 2;
}
.count-container {
	display: flex;
	justify-content: space-between;
	flex: 2;
	align-items: center;
}
.img-icon {
	width: 38px;
	height: 38px;
	position: relative;
	top: -2px;
}
.line {
	display: flex;
	flex-direction: row;
	justify-content: center;
}
.bg-orange {
	background-color: #b57e37;
	height: 48px;
	align-items: center;
	display: flex;
	justify-content: space-around;
	padding: 0 15px 0;
	margin-bottom: 2px;
}
.arrow-size {
	width: 15px;
	height: 15px;
}
.font-name {
	color: #ffffff;
	text-transform: uppercase;
	/* font-size: 16px; */
	/* font-weight: 600; */
	align-items: center;
}
</style>
